import React, { useState } from 'react';

interface ConversationFormProps {
  onSubmitStart: () => void;
  onSubmitEnd: (success: boolean, message: string) => void;
}

const ConversationForm: React.FC<ConversationFormProps> = ({ onSubmitStart, onSubmitEnd }) => {
  const [title, setTitle] = useState('');
  const [content, setContent] = useState('');
  const [type, setType] = useState('question');
  
  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    if (!title.trim() || !content.trim()) {
      onSubmitEnd(false, '');
      return;
    }
    
    onSubmitStart();
    
    try {
      // GitHub Issues API
      //  GitHub API
      
      // 
      await new Promise(resolve => setTimeout(resolve, 1000));
      
      // 
      onSubmitEnd(true, '');
      
      // 
      setTitle('');
      setContent('');
      setType('question');
      
    } catch (error) {
      console.error('Conversation submission error:', error);
      onSubmitEnd(false, '');
    }
  };

  return (
    <form onSubmit={handleSubmit} className="space-y-4">
      <div>
        <label htmlFor="title" className="block text-sm text-gray-400 mb-1"></label>
        <input
          id="title"
          type="text"
          value={title}
          onChange={(e) => setTitle(e.target.value)}
          placeholder=""
          className="nao-input w-full"
          required
        />
      </div>
      
      <div>
        <label htmlFor="content" className="block text-sm text-gray-400 mb-1"></label>
        <textarea
          id="content"
          value={content}
          onChange={(e) => setContent(e.target.value)}
          placeholder=""
          className="nao-input w-full h-40"
          required
        ></textarea>
        <p className="text-xs text-gray-400 mt-1">
          Markdown
        </p>
      </div>
      
      <div>
        <label htmlFor="type" className="block text-sm text-gray-400 mb-1"></label>
        <select
          id="type"
          value={type}
          onChange={(e) => setType(e.target.value)}
          className="nao-input w-full"
        >
          <option value="question"></option>
          <option value="reflection"></option>
          <option value="idea"></option>
          <option value="feedback"></option>
        </select>
      </div>
      
      <button type="submit" className="nao-button w-full">
        
      </button>
    </form>
  );
};

export default ConversationForm;
